
import XImg from 'vux/src/components/x-img/index.vue'

import VueDB from '../util/vue-db/vue-db-long.js'
import Swiper from '../../static/swiper/swiper-4.3.5.min';

var DB = new VueDB();

export default {
name: 'swiperWork',
data() {
return {
}
},
props:{
cscItem:Array,
default:function(){
return [];
}
},
computed: {
},
mounted: function() {

var swiper = new Swiper('.swiper-container', {
slidesPerView: 6,
spaceBetween:30,
breakpoints: {

1400: {
slidesPerView: 5,
spaceBetween:15,
},
1100: {
slidesPerView: 4,
spaceBetween:15,
},
750: {
slidesPerView: 3,
spaceBetween:15,
}
},

roundLengths : true,
loop:true,
loopAdditionalSlides : 1,
autoplay:true,
speed:100,
delay:100,
grabCursor:true, //
pagination: {
el: '.swiper-pagination',
type: 'fraction',
},
navigation: {
nextEl: '.swiper-button-next',
prevEl: '.swiper-button-prev',
},
});
},
components:{
Swiper,
XImg
}
}



